h1, h2, h3, h4, h5, h6 {
  font-family: "roboto", "Georgia";
}

header {
  background-color: #fff;
  display: flex;
  padding: 1rem;
  position: sticky;
  top: 0;
  z-index: 1;
}

header .logo {
  align-items: center;
  display: flex;
  font-size: 24px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

header #search-form {
  margin-right: 1rem;
}

header #search-form--form {
  background-color: #f9f9f9;
  display: none;
  padding: 1rem;
}

header #search-form--form.open {
  align-items: center;
  display: flex;
  left: 0;
  position: absolute;
  top: 100%;
  width: 100%;
  z-index: 1;
}

header #search-form--form label {
  display: block;
  font-weight: 400;
  margin-bottom: .5rem;
  text-align: center;
  padding: 0 1rem 0 0;
}

header #search-form--form input {
  background-color: #eee;
  border: 0px;
  padding: 1rem;
  width: 100%;
}

header #search-form--form button {
  background-color: black;
  color: #fff;
  flex: 1 0 auto;
  padding: 1rem;
}

header .nav-container {
  display: flex;
  margin-left: auto;
}

header .main-nav {
  align-items: center;
  display: flex;
}

header .main-nav--toggler {
  position: relative;
  height: 25px;
  width: 35px;
}

header .main-nav--toggler.active span {
  display: none;
}

header .main-nav--toggler.active span:first-child, header .main-nav--toggler.active span:last-child {
  display: block;
}

header .main-nav--toggler.active span:first-child {
  transform: rotate(45deg);
  top: 50%;
}

header .main-nav--toggler.active span:last-child {
  transform: rotate(135deg);
  top: 50%;
}

header .main-nav--toggler span {
  background-color: black;
  display: block;
  height: 1px;
  position: absolute;
  top: 12px;
  transition: .4s all;
  width: 35px;
}

header .main-nav--toggler span:first-child {
  top: 0;
}

header .main-nav--toggler span:last-child {
  top: 24px;
}

header .main-nav--items {
  display: none;
}

header .main-nav--items.open {
  background-color: #f9f9f9;
  display: block;
  left: 0;
  position: absolute;
  top: 100%;
  width: 100%;
}

header .main-nav--items li {
  padding: 1rem;
  border-bottom: 1px solid #eee;
}

footer {
  background-color: #333333;
  color: #fff;
  padding: 2rem;
  position: relative;
  text-align: center;
}

footer a {
  color: #fff;
}

footer .footer-nav--items {
  display: flex;
  justify-content: center;
}

footer .footer-nav--item {
  display: flex;
  font-size: 12px;
  text-transform: uppercase;
}

footer .footer-nav--item:after {
  background-color: #666;
  content: '';
  display: block;
  margin: 0 0.5rem;
  width: 1px;
}

footer .footer-nav--item:last-child:after {
  content: none;
}

footer .back-to-top {
  bottom: 0;
  padding: 2rem;
  position: absolute;
  right: 0;
  text-align: right;
}

[role="button"],
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  background: none;
  border: 0;
  color: inherit;
  /* cursor: default; */
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  -webkit-appearance: button;
  /* for input */
  -webkit-user-select: none;
  /* for button */
  -moz-user-select: none;
  -ms-user-select: none;
  text-decoration: none !important;
}

/* Make `a` like a button */
[role="button"] {
  color: inherit;
  cursor: default;
  text-align: center;
  text-decoration: none;
  white-space: pre;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.intro {
  text-align: center;
  margin: 0 auto 2rem;
  max-width: 500px;
}

.intro h1 {
  font-weight: 100;
  letter-spacing: 4px;
  margin-top: 0;
  text-transform: uppercase;
}

.properties {
  margin: 0 auto;
  max-width: 960px;
}

@media (min-width: 760px) {
  .properties {
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: auto auto;
  }
}

@media (min-width: 960px) {
  .properties {
    grid-template-columns: auto auto auto;
  }
}

.properties--property {
  margin: 0 auto 1rem;
  max-width: 350px;
  position: relative;
}

@media (min-width: 760px) {
  .properties--property {
    margin: 0 auto;
  }
}

.properties--property h3 {
  bottom: 1rem;
  left: 1rem;
  margin: 0;
  position: absolute;
}

.properties--property img {
  max-width: 100%;
}

.properties--property-nav {
  display: flex;
  opacity: 0;
  position: absolute;
  right: 1rem;
  top: 1rem;
  transition: .4s opacity;
}

.properties--property:hover .properties--property-nav,
.properties--property:focus .properties--property-nav {
  opacity: 1;
}

.properties--property-nav a {
  border: 1px solid black;
  border-radius: 50px;
  color: black;
  display: block;
  margin-right: 0.5rem;
  padding: 0.5rem;
  transition: .4s all;
  will-change: color, background-color;
}

.properties--property-nav a:last-child {
  margin-right: 0;
}

.properties--property-nav a:hover, .properties--property-nav a:focus {
  background-color: black;
  color: #fff;
}
